<template>
  <div class="section">
    <div class="tabs">
      <div :class="{active:tab == 1}" @click="tab = 1">设计数据</div>
      <div :class="{active:tab == 2}" @click="tab = 2">实钻数据</div>
    </div>
    <el-table :data="list" border style="width: 100%">
      <template v-for="(item,i) in arr">
        <el-table-column v-if="item.child" :label="item.txt" :key="i">
          <el-table-column v-for="(l,j) in item.child" :prop="l.prop" :label="l.txt" :key="j" />
        </el-table-column>
        <el-table-column v-else :prop="item.prop" :label="item.txt" :key="i" />
      </template>
    </el-table>
  </div>
</template>

<script>
import request from "@/utils/request";
export default {
  props: ["route"],
  data() {
    return {
      tab: 1,
      list: [],
      url: "/data/query/trajectoryprofiledata",
      arr: [
        {
          txt: "井深",
          prop: "depth"
        },
        {
          txt: "垂深",
          prop: "vertical_depth"
        },
        {
          txt: "井斜角°",
          prop: "inc"
        },
        {
          txt: "方位角°",
          prop: "az"
        },
        {
          txt: "闭合方位°",
          prop: "close_az"
        },
        {
          txt: "闭合距m",
          prop: "close_dis"
        },
        {
          txt: "南北m",
          prop: "nourth_south"
        },
        {
          txt: "东西m",
          prop: "east_west"
        },
        {
          txt: "狗腿度",
          prop: "gtd"
        }
      ]
    };
  },

  components: {
  },

  computed: {},

  mounted: function() {
    this.getData()
  },

  methods: {
    getData() {
      let data = this.route;
      request({
        url: "/data/query/trajectoryprofiledata",
        method: 'post',
        data
      }).then(res => {
          this.list = res;
      });
    }
  }
};
</script>





